/* src/app/agregar-tarea/agregar-tarea.page.scss */
.form-content {
  --background: var(--ion-background-color);
}
.main-title {
  font-weight: 700;
  letter-spacing: -0.5px;
}
.custom-form {
  max-width: 600px;
  margin: 0 auto;
}
.input-group {
  margin-bottom: 24px;
}
.input-label {
  display: block;
  font-size: 14px;
  font-weight: 600;
  color: var(--ion-color-medium);
  margin-bottom: 8px;
  margin-left: 4px;
}
.custom-input {
  --background: #fff;
  --border-radius: 12px;
  --padding-start: 16px;
  --inner-padding-end: 16px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.03);
  border: 1px solid rgba(0, 0, 0, 0.05);
}
.custom-input ion-input,
.custom-input ion-textarea {
  --padding-top: 12px;
  --padding-bottom: 12px;
}
body.dark .custom-input {
  --background: #2a2a2a;
  border: 1px solid rgba(255, 255, 255, 0.05);
}
.textarea-input {
  align-items: flex-start;
}
.error-msg {
  font-size: 12px;
  color: var(--ion-color-danger);
  margin-top: 6px;
  margin-left: 4px;
  display: flex;
  align-items: center;
  gap: 4px;
}
.color-section {
  margin-bottom: 32px;
}
.color-row {
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
  padding: 4px;
}
.color-swatch {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  border: 2px solid transparent;
  background: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.2s ease;
  position: relative;
}
.color-swatch.selected {
  border-color: var(--ion-color-primary);
  transform: scale(1.1);
}
.color-swatch ion-icon {
  font-size: 24px;
  color: #333;
  opacity: 0.7;
}
.dot {
  position: absolute;
  top: 4px;
  left: 4px;
  right: 4px;
  bottom: 4px;
  border-radius: 50%;
}
.dot.sun {
  background-color: #fff4cc;
  background-color: var(--note-sun);
}
.dot.sky {
  background-color: #e6f2ff;
  background-color: var(--note-sky);
}
.dot.mint {
  background-color: #e7fff2;
  background-color: var(--note-mint);
}
.dot.lavender {
  background-color: #efe9ff;
  background-color: var(--note-lavender);
}
.dot.peach {
  background-color: #ffe9df;
  background-color: var(--note-peach);
}
.dot.gray {
  background-color: #f2f3f5;
  background-color: var(--note-gray);
}
.save-btn {
  --box-shadow: 0 8px 20px rgba(98, 0, 238, 0.3);
  margin-top: 16px;
  height: 52px;
  font-size: 16px;
}
/*# sourceMappingURL=agregar-tarea.page.css.map */
